import {
    Component,
    h,
    Host,
    Prop,
    Element,
    State,
    Method,
    Watch,
} from '@stencil/core';
import {
    computePosition,
    flip,
    shift,
    offset,
    autoUpdate,
    type Placement,
    size,
} from '@floating-ui/dom';

export type TooltipPosition = 'top' | 'bottom' | 'left' | 'right';
export type TooltipAlign = 'start' | 'end' | 'center';
const DEFAULT_TOOLTIP_MAX_WIDTH = 200;

/**
 * @category **Overlays**
 * @description **A tooltip popover component that displays contextual information when hovering over or focusing on an element.**
 *
 * @slot tooltip-trigger - Shows the tooltip when hovered (only used when trigger_id is not provided)
 * @slot default - Content to go inside the tooltip
 */
@Component({
    tag: 'at-tooltip',
    shadow: false,
})
export class AtTooltip {
    /**
     * Position of opened tooltip element relative to the trigger element.
     */
    @Prop() position: TooltipPosition = 'top';

    /**
     * Alignment of opened tooltip element relative to trigger element.
     */
    @Prop() align: TooltipAlign = 'center';

    /**
     * Prevent opening tooltip
     */
    @Prop() disabled: boolean = false;

    /**
     * Maximum width constraint for the tooltip in pixels. Defaults to 300px for readability.
     */
    @Prop() width?: string = '200px';

    /**
     * Offset in pixels from the edge of the trigger element
     */
    @Prop() offset?: number = 8;

    /**
     * Delay before showing and hiding the tooltip when interacting with the trigger element.
     */
    @Prop() delay?: number = 150;

    /**
     * Data-id of an external element to use as the trigger. When provided, the trigger slot is not needed.
     */
    @Prop() trigger_id?: string;

    @Watch('disabled')
    async disabledChanged(newValue: boolean) {
        if (newValue && this.isOpen) {
            await this.closeTooltip();
        }
    }

    @State() isOpen = false;

    @Element() el: HTMLElement;
    triggerEl: HTMLElement;
    tooltipEl: HTMLElement;
    private triggerEls: HTMLElement[] = [];

    private cleanupAutoUpdate?: () => void;
    private popoverId: string;
    private showTimeout?: NodeJS.Timeout;
    private hideTimeout?: NodeJS.Timeout;

    private updatePosition = async () => {
        if (this.triggerEl && this.tooltipEl && this.isOpen) {
            await this.updateFloatingPosition();
        }
    };

    /**
     * Opens the tooltip.
     */
    @Method()
    async openTooltip(): Promise<void> {
        if (this.disabled) return;

        if (this.tooltipEl) {
            this.tooltipEl.showPopover();
            this.isOpen = true;
        }
        await this.updatePosition();
        this.updateAriaExpanded();
    }

    /**
     * Closes the tooltip.
     */
    @Method()
    async closeTooltip(): Promise<void> {
        if (this.tooltipEl) {
            this.tooltipEl.hidePopover();
            this.isOpen = false;
        }
        this.updateAriaExpanded();
    }

    private updateAriaExpanded(): void {
        if (this.trigger_id && this.triggerEl) {
            this.triggerEl.setAttribute(
                'aria-expanded',
                this.isOpen ? 'true' : 'false',
            );
        }
    }

    async componentDidLoad() {
        this.popoverId = `atui-tooltip-${Math.random().toString(36).substr(2, 9)}`;

        if (this.trigger_id) {
            this.triggerEls = Array.from(
                document.querySelectorAll(`[data-id="${this.trigger_id}"]`),
            ) as HTMLElement[];
            if (this.triggerEls.length === 0) {
                console.warn(
                    `atui-tooltip: No elements found with data-id="${this.trigger_id}"`,
                );
                return;
            }
        } else {
            this.triggerEl = this.el.querySelector(
                '[data-name="tooltip-trigger"]',
            );
        }

        await this.setupFloatingUI();
        setTimeout(() => this.setupPopoverEventListeners(), 0);

        if (this.trigger_id && this.triggerEls.length) {
            this.setupExternalTriggerListeners();
        }
    }

    disconnectedCallback() {
        this.cleanupFloatingUI();
        this.cleanupExternalTriggerListeners();

        if (this.showTimeout) {
            clearTimeout(this.showTimeout);
            this.showTimeout = undefined;
        }
        if (this.hideTimeout) {
            clearTimeout(this.hideTimeout);
            this.hideTimeout = undefined;
        }
    }

    private externalTriggerListeners: Array<{
        element: HTMLElement;
        event: string;
        handler: EventListener;
    }> = [];

    private cleanupExternalTriggerListeners() {
        this.externalTriggerListeners.forEach(({ element, event, handler }) => {
            element.removeEventListener(event, handler);
        });
        this.externalTriggerListeners = [];
    }

    private async setupPopoverEventListeners() {
        if (this.tooltipEl) {
            this.tooltipEl.addEventListener('toggle', (event: Event) => {
                const customEvent = event as any;
                this.isOpen = customEvent.newState === 'open';

                if (this.isOpen) {
                    requestAnimationFrame(() => {
                        this.tooltipEl.style.opacity = '1';
                        this.updatePosition();
                    });
                } else {
                    this.tooltipEl.style.opacity = '0';
                }
            });
        }
    }

    private setupExternalTriggerListeners() {
        if (!this.triggerEls || this.triggerEls.length === 0) return;

        this.triggerEls.forEach((el) => {
            const mouseEnterHandler = () => {
                if (!this.disabled) {
                    this.triggerEl = el;
                    this.mouseEnterHandler();
                }
            };

            const mouseLeaveHandler = () => {
                if (!this.disabled) {
                    this.triggerEl = el;
                    this.mouseLeaveHandler();
                }
            };

            el.addEventListener('mouseenter', mouseEnterHandler);
            el.addEventListener('mouseleave', mouseLeaveHandler);

            this.externalTriggerListeners.push(
                {
                    element: el,
                    event: 'mouseenter',
                    handler: mouseEnterHandler,
                },
                {
                    element: el,
                    event: 'mouseleave',
                    handler: mouseLeaveHandler,
                },
            );

            el.setAttribute('aria-haspopup', 'true');
            el.setAttribute('aria-expanded', 'false');
        });
    }

    private async mouseEnterHandler(): Promise<void> {
        if (this.hideTimeout) {
            clearTimeout(this.hideTimeout);
            this.hideTimeout = undefined;
        }

        if (this.isOpen) return;

        if (this.showTimeout) {
            clearTimeout(this.showTimeout);
        }

        this.showTimeout = setTimeout(async () => {
            if (!this.disabled && !this.isOpen) {
                await this.openTooltip();
            }
            this.showTimeout = undefined;
        }, this.delay || 0);
    }

    private async mouseLeaveHandler(): Promise<void> {
        if (this.showTimeout) {
            clearTimeout(this.showTimeout);
            this.showTimeout = undefined;
        }

        if (!this.isOpen) return;

        const hideDelay = Math.min(this.delay || 0, 150);
        this.hideTimeout = setTimeout(async () => {
            if (this.isOpen) {
                await this.closeTooltip();
            }
            this.hideTimeout = undefined;
        }, hideDelay);
    }

    private async setupFloatingUI(): Promise<void> {
        if (this.cleanupAutoUpdate) {
            this.cleanupAutoUpdate();
        }

        if (this.triggerEl && this.tooltipEl) {
            const observer = new IntersectionObserver(
                (entries) => {
                    if (!entries[0].isIntersecting && this.isOpen) {
                        this.closeTooltip();
                    }
                },
                { threshold: 0 },
            );
            observer.observe(this.triggerEl);

            this.cleanupAutoUpdate = autoUpdate(
                this.triggerEl,
                this.tooltipEl,

                () => {
                    if (this.isOpen) {
                        const placement = this.getFloatingUIPlacement();
                        const strategy = 'fixed';

                        computePosition(this.triggerEl, this.tooltipEl, {
                            placement,
                            strategy,
                            middleware: [
                                offset(this.offset ?? 8),
                                flip({
                                    fallbackStrategy: 'bestFit',
                                    padding: 8,
                                }),
                                shift({
                                    padding: 8,
                                    mainAxis: true,
                                    crossAxis: true,
                                }),
                                size({
                                    apply: ({
                                        availableWidth,
                                        availableHeight,
                                        elements,
                                    }) => {
                                        const userMax =
                                            parseInt(
                                                (
                                                    this.width || '200px'
                                                ).toString(),
                                                10,
                                            ) || DEFAULT_TOOLTIP_MAX_WIDTH;
                                        Object.assign(elements.floating.style, {
                                            maxWidth: `${Math.min(availableWidth, userMax)}px`,
                                            maxHeight: `${availableHeight}px`,
                                        });
                                    },
                                }),
                            ],
                        }).then(({ x, y }) => {
                            const styles: any = {
                                position: strategy,
                                left: `${x}px`,
                                top: `${y}px`,
                                margin: '0',
                                transform: 'none',
                                maxWidth: this.width || '200px',
                            };

                            Object.assign(this.tooltipEl.style, styles);
                        });
                    }
                },
                {
                    ancestorScroll: true,
                    ancestorResize: true,
                    elementResize: true,
                    layoutShift: true,
                    animationFrame: true,
                },
            );

            const originalCleanup = this.cleanupAutoUpdate;
            this.cleanupAutoUpdate = () => {
                originalCleanup();
                observer.disconnect();
            };
        }
    }

    private cleanupFloatingUI() {
        this.cleanupAutoUpdate?.();
        this.cleanupAutoUpdate = undefined;
    }

    private async updateFloatingPosition(): Promise<void> {
        if (!this.triggerEl || !this.tooltipEl) return;
        this.setupFloatingUI();
    }

    private getFloatingUIPlacement(): Placement {
        const positionMap: Record<TooltipPosition, string> = {
            top: 'top',
            bottom: 'bottom',
            left: 'left',
            right: 'right',
        };

        const alignMap: Record<TooltipAlign, string> = {
            start: 'start',
            end: 'end',
            center: 'center',
        };

        const position = positionMap[this.position] || 'bottom';
        const align = alignMap[this.align] || 'center';

        return `${position}-${align}` as Placement;
    }

    render() {
        return (
            <Host class="relative">
                {!this.trigger_id && (
                    <div
                        aria-haspopup="true"
                        data-name="tooltip-trigger"
                        ref={(el) => (this.triggerEl = el)}
                        aria-expanded={`${this.isOpen ? 'true' : 'false'}`}
                        class={this.disabled ? 'contents' : ''}
                        onMouseEnter={() =>
                            !this.disabled ? this.mouseEnterHandler() : null
                        }
                        onMouseLeave={() =>
                            !this.disabled ? this.mouseLeaveHandler() : null
                        }
                    >
                        <slot name="tooltip-trigger" />
                    </div>
                )}

                <div
                    ref={(el) => (this.tooltipEl = el)}
                    data-position={this.position}
                    data-align={this.align}
                    popover="auto"
                    id={this.popoverId}
                    class="pointer-events-none w-fit rounded-md bg-gray-950/80 px-[6px] py-2 text-sm text-white opacity-0 shadow-md transition-opacity duration-200 ease-out"
                    data-name="tooltip-content-wrapper"
                >
                    <slot />
                </div>
            </Host>
        );
    }
}
